<template>
    <div>
        <!-- 상단 버튼 -->
        <TopButton title="리뷰작성" />

        <!-- 리뷰 작성 폼 -->
        <form @submit.prevent="submitReview" enctype="multipart/form-data">
            <div>
                <label for="author">작성자 아이디:</label>
                <!-- 작성자 아이디는 읽기 전용 -->
                <input v-model="author" id="author" placeholder="작성자 아이디" readonly />
            </div>
            <div>
                <label for="title">제목:</label>
                <input v-model="title" id="title" placeholder="리뷰 제목" required />
            </div>
            <div>
                <label for="image">사진 첨부하기:</label>
                <input type="file" id="image" @change="handleFileUpload" accept="image/*" />
            </div>
            <div>
                <label for="content">내용:</label>
                <textarea v-model="content" id="content" placeholder="리뷰 내용을 입력하세요" required></textarea>
            </div>
            <button type="submit">리뷰 등록</button>
        </form>
    </div>
</template>

<script>
import TopButton from '@/components/TopButton.vue';

export default {
    name: 'ReviewForm',
    components: {
        TopButton
    },
    data() {
        return {
            author: '', // 로그인된 사용자 아이디
            title: '', // 리뷰 제목
            content: '', // 리뷰 내용
            image: null // 업로드할 이미지 파일
        };
    },
    methods: {
        // 로그인 상태 확인 및 작성자 아이디 설정
        async checkLoginStatus() {
            try {
                const response = await fetch('http://tnswn102030.dothome.co.kr/api/auth/check_login.php', {
                    method: 'GET',
                    credentials: 'include' // 세션 쿠키 포함
                });
                const result = await response.json();

                if (result.status === 'logged_in') {
                    this.author = result.user_id; // 로그인된 사용자 아이디 설정
                } else {
                    alert('로그인 상태가 아닙니다. 로그인 페이지로 이동합니다.');
                    this.$router.push('/login'); // 로그인 페이지로 이동
                }
            } catch (error) {
                console.error('로그인 상태 확인 중 오류 발생:', error);
            }
        },
        // 파일 업로드 처리
        handleFileUpload(event) {
            this.image = event.target.files[0];
        },
        // 리뷰 등록
        async submitReview() {
            const formData = new FormData();
            formData.append('author', this.author);
            formData.append('title', this.title);
            formData.append('content', this.content);
            if (this.image) {
                formData.append('image', this.image);
            }

            try {
                const response = await fetch('http://tnswn102030.dothome.co.kr/api/posts/create_review.php', {
                    method: 'POST',
                    body: formData,
                    credentials: 'include' // 세션 쿠키 포함
                });
                const result = await response.text();
                alert(result);

                // 리뷰 작성 완료 후 목록 페이지로 이동
                this.$router.push('/reviewpage');
            } catch (error) {
                console.error('리뷰 등록 중 오류 발생:', error);
            }
        }
    },
    mounted() {
        this.checkLoginStatus(); // 컴포넌트가 마운트되었을 때 로그인 상태 확인
    }
};
</script>
<style scoped>
form {
    width: 90%;
    max-width: 500px;
    margin: 0 auto; /* 가운데 정렬 */
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    text-align: left;
}

/* 라벨 스타일 */
label {
    display: inline-block;
    margin-bottom: 5px;
    font-weight: bold;
    font-size: 14px;
    color: #333;
}

/* 입력 필드 스타일 */
input[type='text'],
input[type='file'],
textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 14px;
    color: #555;
}
/* 입력 필드 포커스 스타일 */
input[type='text']:focus,
textarea:focus {
    outline: none;
    border-color: #007bff;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

/* 텍스트 에어리어 높이 조정 */
textarea {
    height: 150px;
    resize: vertical;
}

/* 버튼 스타일 */
button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #999;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
}

button:hover {
    background-color: #333;
}

/* 이미지 파일 선택 스타일 */
input[type='file'] {
    padding: 3px;
}
</style>
