<template>
    <div class="container">
        <TopButton title="리얼리뷰" />
        <div class="row">
            <div class="col-auto me-auto">
                <div class="form-check">
                    <input class="form-check-input" type="checkbox" value="" id="flexCheckChecked" checked />
                    <label class="form-check-label" for="flexCheckChecked"> 내 피부타입만 보기 </label>
                </div>
            </div>
            <div class="col-auto">
                <div class="dropdown">
                    <button
                        class="btn btn-secondary dropdown-toggle"
                        type="button"
                        data-bs-toggle="dropdown"
                        aria-expanded="false"
                    >
                        추천순
                    </button>
                    <ul class="dropdown-menu">
                        <li><button class="dropdown-item" type="button">인기순</button></li>
                        <li><button class="dropdown-item" type="button">신상품순</button></li>
                        <li><button class="dropdown-item" type="button">저가순</button></li>
                        <li><button class="dropdown-item" type="button">고가순</button></li>
                    </ul>
                </div>
            </div>
        </div>
        <!-- 리뷰  -->
        <div class="contents">
            <div class="reviewItem" v-for="review in reviews" :key="review.id">
                <div class="reviewImg">
                    <img :src="review.image_path" alt="리뷰 이미지" v-if="review.image_path" />
                </div>
                <div class="reviewContent">
                    <p>상품명: {{ review.title }}</p>
                    <p>{{ review.content }}</p>
                    <p>유저 아이디: {{ review.user_id || '익명' }}</p>
                </div>
                <div class="reviewActions">
                    <button @click="editReview(review)">수정</button>
                    <button @click="deleteReview(review.id)">삭제</button>
                </div>
            </div>
            <button type="button" @click="navigateToCreate">리뷰작성하기</button>
        </div>
    </div>
</template>
<script>
import TopButton from '@/components/TopButton.vue';

export default {
    name: 'ReviewList',
    components: {
        TopButton
    },
    data() {
        return {
            reviews: [] // 리뷰 데이터를 저장
        };
    },
    methods: {
        // 리뷰 목록 불러오기
        async fetchReviews() {
            try {
                const response = await fetch('http://tnswn102030.dothome.co.kr/api/posts/fetch_reviews.php');
                this.reviews = await response.json();
            } catch (error) {
                console.error('리뷰 데이터를 가져오는 중 오류 발생:', error);
            }
        },
        // 리뷰 작성 페이지로 이동
        navigateToCreate() {
            this.$router.push('/reviews/create');
        },
        // 리뷰 삭제
        async deleteReview(id) {
            if (confirm('정말로 삭제하시겠습니까?')) {
                try {
                    const response = await fetch('http://tnswn102030.dothome.co.kr/api/posts/delete_review.php', {
                        method: 'POST',
                        headers: { 'Content-Type': 'application/json' },
                        body: JSON.stringify({ id })
                    });
                    const result = await response.text();
                    alert(result);
                    this.fetchReviews(); // 리뷰 목록 갱신
                } catch (error) {
                    console.error('리뷰 삭제 중 오류 발생:', error);
                }
            }
        },
        // 리뷰 수정 (현재는 테스트 상태)
        editReview(review) {
            alert(`수정 기능은 별도로 구현해야 합니다. 리뷰 ID: ${review.id}`);
        }
    },
    mounted() {
        this.fetchReviews(); // 컴포넌트가 마운트되었을 때 리뷰 데이터를 가져옴
    }
};
</script>

<style scoped>
/* 리뷰 */
.contents {
    width: 100%;
    margin: 0 auto;
}
.reviewItem {
    width: 90%;
    display: flex;
    margin: 20px;
}
.reviewImg {
    width: 125px;
    height: 150px;
    background-color: #999;
    margin-right: 20px;
}
.reviewImg img {
    display: block;
    width: 125px;
    height: 150px;
}
.reviewContent {
    text-align: left;
}
.reviewContent p {
    margin: 0;
}
</style>
